import React from 'react'
import dubaiPackage from '../images/dubaiPackage.jpg';

const PackagesItems = () => {
    return (
        <div className="col" data-bs-toggle="modal" data-bs-target="#exampleModal">
            <div className="card bg-dark text-white h-100 cardHover">
                <img src={dubaiPackage} className="card-img packagesCardItems" alt="..." />
                <div className="card-img-overlay gradientCard">
                    <div className='packagesDetails'>
                        <h5 className="card-title">Card title</h5>
                        <p className="card-text">This is a wider card with supporting text below as a natural lead-in to additional content. This content is a little bit longer.</p>
                        <p className="card-text">Last updated 3 mins ago</p>
                    </div>
                </div>
            </div>
        </div>
    )
}

export default PackagesItems